body,
main,
section,
article,
div,
h1,
h2,
h3,
h4,
h5,
h6,
p,
a,
img,
button,
input,
textarea,
span,
footer,
ul,
li,
aside {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

input:focus,
textarea:focus {
  outline: none;
}

.display-none {
  display: none;
}
